/* Lorem Ipsum One */
.Lorem-one {
  padding: 60px 0;
  text-align: left;
  .section-title {
    h2 {
      text-align: left;
      position: relative;
      color: #333;
      font-size: 30px;
      text-transform: uppercase;
      font-weight: 400;
      &::after {
        content: "";
        position: absolute;
        bottom: -10px;
        left: 0;
        width: 70px;
        height: 1px;
        background-color: $mainColor;
      }
    }
    p {
      color: #333;
      font-size: 18px;
      font-weight: 300;
      line-height: 28px;
      margin-top: 35px;
    }
  }
}

