/* Lorem Ipsum Three */
.lorem-three {
  padding: 60px 0;
  text-align: left;
  .section-title {
    h2 {
      text-align: left;
      position: relative;
      color: #333;
      font-size: 30px;
      text-transform: uppercase;
      font-weight: 400;
      &::after {
        content: "";
        position: absolute;
        bottom: -10px;
        left: 0;
        width: 70px;
        height: 1px;
        background-color: $mainColor;
      }
    }
    p {
      color: #333;
      font-size: 18px;
      font-weight: 300;
      line-height: 28px;
      margin-top: 25px;
      margin-bottom: 50px;
    }
  }
  .row { margin-bottom: 50px }
  .info {
    position: relative;
    height: 350px;
    overflow: hidden;
    box-shadow: 0 0px 8px 0 rgba(0,0,0,.6);
    transition: .3s;
    margin-bottom: 50px;
    .img {
      position: absolute;
      height: 100%;
      z-index: 1;
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(#000, .3)
      }
      img { transition: .3s }
    }
    .text {
      position: absolute;
      bottom: 15px;
      left: 15px;
      z-index: 99;
      width: 90%;
      height: auto;
      background: #fff;
      padding: 15px;
      h3 {
        color: $mainColor;
        font-size: 18px;
        letter-spacing: 1px;
        text-transform: uppercase
      }
      p {
        font-size: 14px;
        color: #333
      }
    }
    &:hover {
      img {
        transform: scale(1.1)
      }
      box-shadow: -2px 8px 22px 0 rgba(0,0,0,.15)
    }
  }
}

