/* Header */
.landingColumn1 {
  background-image: url("../images/splash.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: left;
  align-items: center;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(#000, .5);
    z-index: 1;
  }
  .tempHeader {
    position: relative;
    z-index: 999;
    h2 {
      width: 600px;
      color: #FFF;
      font-size: 50px;
      font-weight: 300;
      line-height: 65px;
      padding-bottom: 20px;
      text-transform: uppercase
    }
  }
  .viewDeals {
    position: relative;
    z-index: 99;
    background-color: rgba(#000, .5);
    border: 1px solid #868687;
    color: #A9AD88;
    padding: 10px 30px;
    font-size: 18px;
    text-transform: uppercase;
    border-radius: 3px;
    transition: all .3s;
    &:hover {
      background-color: $mainColor;
      border-color: $mainColor;
      color: #fff
    }
    &:focus {
      outline: 0;
    }
  }
}