/* Footer */
footer {
    background-color: #1a1a1a;
    border-top: 5px solid $mainColor;
    padding: 30px 0;
    .scoail {
        text-align: center;
        .btn {
            padding: 5px;
            &:focus {
                outline: 0;
                box-shadow: none
            }
            i {
                font-size: 18px;
                width: 40px;
                height: 40px;
                line-height: 40px;
                border-radius: 3px;
                text-align: center;
                color: #fff;
                background-color: #0e1016;
                transition: .3s ease;
            }
            &:nth-child(1):hover {
                i {
                    background-color: #3b5999
                }
            }
            &:nth-child(2):hover {
                i {
                    background-color: #55acee
                }
            }
            &:nth-child(3):hover {
                i {
                    background-color: #e4405f
                }
            }
            &:nth-child(4):hover {
                i {
                    background-color: #0077B5
                }
            }
        }
    }
    .copyright {
        text-align: center;
        margin-top: 15px;
        p {
            color: #8C888A;
            font-size: 15px;
            font-weight: 600;
            text-transform: uppercase;
        }
    }
}